
@media (min-width: 1000px) {

    .info-container {
        height: 600px;
        width: 300px;
        background-color:#00283a;
        position: absolute;
        /* z-index: -5; */
        top: 150px;
        left: 150px;
        border-radius: 10px;
        padding: 40px;
    }
}

.imageContainer {
    width: 100px;
    height: 100px;
    background-color: red;
    margin: 20px;
}

.image {
    width: 120px;
    height: 120px;
    border-radius: 120px;
    margin: 40px;
    object-fit: cover;
    margin-bottom: 5px;
}

.firstNameCard {
    color: #dedee0;
    font-weight: 700;
    font-size: 18px;
    /* background-color: greenyellow; */
  }

  .desigText {
    color: #dedee0;
    font-weight: 700;
    font-size: 12px;
    margin-top: -5px;
  }

  .extraDetails {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-left: 40px;
    margin-right: 40px;
    /* margin-bottom: 5px; */
  }

  .detailText {
    color: #dedee0;
    font-weight: 700;
    font-size: 11px;
    margin-top: 0px;
  }

  .iconContainer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    column-gap: 25px;
    align-items: center;
    margin-top: 30px;
    margin-bottom: 20px;
    padding-top: 50px;
    padding-bottom: 50px;
    border: 2px dotted #ffffff23;
    border-left-width: 0px;
    border-right-width: 0px;
  }

  .extra-detail-container {
    margin-top: 40px;
    margin-bottom: 40px;
    border: 2px dotted #ffffff23;
    border-left-width: 0px;
    border-right-width: 0px;
    border-top-width: 0px;
    padding-bottom: 50px;
  }

  @media (max-width: 1000px) {
    .info-container {
        height: 600px;
        width: 90%;
        background-color:#00283a;
        /* position: absolute; */
        /* top: 150px;
        left: 50px; */
        /* z-index: -5; */
        margin-top: -50px;
        border-radius: 10px;
    }
    
  }