.App {
  text-align: center;
  padding-left: 10px;
  padding-right: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.extra-container {
  height: 10px;
  background-color: #00151f;
  position: sticky;
  top: 0px;
  width: 100%;
  display: flex;
  z-index: 10;
}

@media (max-width: 1000px) {
  .App {
    text-align: center;
    padding-left: 0px;
    padding-right: 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .extra-container {
    height: 0px;
    background-color: #00151f;
    position: sticky;
    top: 0px;
    width: 100%;
    display: none;
  }
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

p {
  font-family: 'Inter', sans-serif;
}

.App-header {
  /* background-color: #00283a; */
  margin: 10px;
  border-radius: 10px;
  /* background-color: #00283a; */
  height: 60px;
  /* padding-left: 20px; */
  /* min-height: 100vh; */
  /* height: 200vh; */
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  font-size: calc(7px + 2vmin);
  color: white;
  box-shadow: 1px 1px 2px 1px #00283a;
}

.header-name {
  color: '#afb42b';
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
